@font-face {
  font-family: 'Peter Mono';
  src: url("../public/fonts/peter_mono_comfortable.otf") format("opentype");
}

.main-wrapper {
  width: 100%;
  margin: 0 auto;
}

.main-content,
.left-column,
.right-column {
  margin: 0 auto;
}

.left-column {
  padding: 25px;
}

@media only screen and (min-width: 900px) {
  .main-wrapper {
    width: 900px;
  }

  .left-column {
    width: 600px;
    float: left;
  }

  .right-column {
    width: 250px;
    float: right;
    text-align: center;
    margin-top: 40px;
  }
}

/* Global Styling */

html,
body,
a,
header,
nav,
p,
div,
span,
img {
  transition: background, color, transform 250ms ease-out;
}

body {
  font-family: 'Dosis', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #505962;
  background: #f6f6f6;
  padding: 0;
  margin: 0;
  min-width: 454px;
}

a {
  color: #4788b7;
  text-decoration: none;
}

a:hover {
  color: #4c9c7b;
}

div {
  margin: 0;
  padding: 0;
}

h1 {
  color: #1a1a1a;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 600;
}

h2 {
  color: #1a1a1a;
  margin-top: 60px;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
}

h3 {
  color: #1a1a1a;
  margin: 0;
  font-size: 19px;
}

img {
  border-radius: 4px;
}

p {
  padding: 0;
  margin: 0;
}

ul {
  padding: 0 0 0 20px;
  margin: 0 0 0 17px;
}

.hidden {
  display: none;
}

.arrow {
  cursor: pointer;
  display: inline-block;
  height: 6px;
  margin-bottom: 3px;
  margin-left: 6px;
}

.drop-down {
  transform: rotate(90deg);
}

.pull-up {
  transform: rotate(-90deg);
}

header {
  width: 100%;
  margin: 0;
  padding: 0;
}

nav {
  font-family: 'Dosis', sans-serif;
  font-size: 20px;
  text-align: center;
  font-weight: 300;
  letter-spacing: 1px;
  width: 100%;
  padding: 10px 20px 10px 0;
  position: fixed;
  z-index: 10;
  box-shadow: 0 3px 2px rgba(0, 0, 0, 0.1);
  transition: background 250ms ease-out;
}

.nav-about {
  background: #4788b7;
}

.nav-experience {
  background: #4c9c7b;
}

.nav-portfolio {
  background: #5e5e65;
}

.nav-contact {
  background: #4c9cc1;
}

.nav-underlay {
  height: 36px;
}

.nav-link {
  margin: 0 20px 0 20px;
  font-weight: 400;
  color: #ffffff;
  text-decoration: none;
}

.nav-link-active {
  margin: 0 20px 0 20px;
  font-weight: 400;
  text-decoration: none;
  color: #f4e6c2;
  cursor: default;
}

.nav-link-active:hover {
  color: #f4e6c2;
}

.nav-link:hover {
  color: #f4e6c2;
}

@media only screen and (min-width: 900px) {
  .nav-link,
  .nav-link-active {
    font-size: 16px;
  }
}

.side-bar {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  background: #ffffff;
  border-radius: 3px;
  padding: 20px;
  padding: 40px 20px;
  text-align: center;
}

.side-bar-headshot-image {
  border-radius: 160px;
}

.side-bar-content {
  width: 218px;
  margin: 0 auto;
  padding: 10px;
}

.side-bar-content-name {
  margin: 10px 0;
  padding-bottom: 10px;
}

.side-bar-content-social {
  transition: filter 250ms ease-out;
  margin: 0 6px;
  filter: grayscale(30%);
}

.side-bar-content-social:hover {
  filter: grayscale(100%);
}

.side-bar-content-social-git {
  transition: filter 250ms ease-out;
  margin: 0 6px;
  filter: contrast(80%);
}

.side-bar-content-social-git:hover {
  filter: contrast(25%);
}

@media only screen and (min-width: 900px) {
  .side-bar {
    position: fixed;
    padding: 20px;
  }
}

.about-title {
  color: #4788b7;
}

.about-elevator-pitch {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  background: #ffffff;
  border-radius: 3px;
  padding: 20px;
  clear: both;
  margin-bottom: 25px;
}

.about-elevator-pitch-title {
  cursor: pointer;
}

.about-elevator-pitch-paragraph {
  padding-top: 8px;
  margin-bottom: 0;
}

.about-code-list {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  background: #ffffff;
  border-radius: 3px;
  padding: 20px;
  margin-top: 10px;
  margin-bottom: 25px;
  list-style: none;
}

.about-code-list-title {
  margin-bottom: 10px;
}

.about-code-list-item {
  display: flex;
  align-items: center;
  font-family: 'Peter Mono', 'Dosis', 'monospace';
}

.about-code-list-item-name {
  margin-right: 6px;
}

.about-code-list-item-icon {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px 8px 4px 20px;
}

.about-code-list-item-icon-image {
  max-width: 100%;
  max-height: 100%;
  border-radius: 0;
}

.contact-title {
  color: #4c9cc1;
}

.contact-info {
  border-radius: 3px 3px 0 0;
  margin-bottom: 0;
  background: #ffffff;
  padding: 20px;
  line-height: 1.5;
  font-size: 14px;
}

.contact-map {
  border-radius: 0 0 3px 3px;
  margin-top: 0;
  background: #ffffff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  border-radius: 0 0 3px 3px;
  margin: auto;
  margin-bottom: 25px;
  min-width: 100%;
}

@media only screen and (min-width: 900px) {
  .contact-map {
    min-width: 426px;
  }
}

.experience-title {
  color: #4c9c7b;
}

.experience-pdf-tip {
  font-family: 'Peter Mono', 'Dosis', 'monospace';
  font-size: 13px;
  text-decoration: none;
  color: #999fac;
  display: inline-block;
  height: 100%;
  vertical-align: super;
  margin-left: 6px;
}

.experience-item {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  background: #ffffff;
  border-radius: 3px;
  padding: 20px;
  overflow: auto;
  margin-bottom: 25px;
}

.experience-item-content {
  float: left;
  width: 100%;
}

.experience-item-left {
  display: inline-block;
  padding-right: 20px;
  vertical-align: top;
  width: 50px;
}

.experience-item-right {
  display: inline-block;
  width: calc(100% - 70px);
}

.experience-item-icon {
  width: 50px;
}

.experience-item-title {
  margin-bottom: 10px;
  cursor: pointer;
}

.experience-item-title-no-pointer {
  margin-bottom: 10px;
}

.experience-item-date {
  font-family: 'Peter Mono', 'Dosis', 'monospace';
  color: #999fac;
  font-size: 14px;
}

.experience-item-dot {
  font-size: 19px;
  font-weight: 700;
  margin: 0 6px;
}

.experience-item-org {
  margin-left: 0;
}

.experience-item-bullets {
  padding-left: 0;
  padding-top: 16px;
  white-space: normal;
}

.portfolio-title {
  color: #5e5e65;
}

.portfolio-item {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  margin-bottom: 25px;
}

.portfolio-item-icon {
  border-radius: 3px 3px 0 0;
  margin-bottom: 0;
  background: #ffffff;
}

.portfolio-item-icon-image {
  border-radius: 3px 3px 0 0;
  margin-bottom: 0;
  background: #ffffff;
  display: block;
  margin: auto;
  max-width: 100%;
}

.portfolio-item-icon-image-small {
  border-radius: 23px 23px 23px 23px;
  padding: 20px;
}

.portfolio-item-content {
  border-radius: 0 0 3px 3px;
  margin-top: 0;
  background: #ffffff;
  padding: 20px;
}

.portfolio-item-title {
  cursor: pointer;
}

.portfolio-item-dot {
  margin: 0 6px;
}

.portfolio-item-org {
  margin-left: 6px;
}

.portfolio-item-link {
  font-size: 16px;
}

.portfolio-item-description {
  padding-top: 25px;
}

.portfolio-item-date {
  font-family: 'Peter Mono', 'Dosis', 'monospace';
  color: #999fac;
  font-size: 14px;
}

.portfolio-item-keywords {
  font-family: 'Peter Mono', 'Dosis', 'monospace';
  font-size: 13px;
  text-decoration: none;
  color: #999fac;
}

.portfolio-item-bullets {
  margin-top: 20px;
}

.poster {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  width: 100%;
  border-radius: 4px;
  margin-top: 40px;
  cursor: pointer;
}

.poster:hover {
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.08);
}

.poster-modal {
  width: 100%;
  padding: 0;
  border-radius: 4px;
}




